
.settings-overlay {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2200;
  padding: 20px;
  overflow: auto;
}

.settings-modal {
  background-color: #f5f5dc;
  border: 4px solid #000;
  width: 90%;
  max-width: 700px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  position: relative;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 2px solid #666;
    pointer-events: none;
    z-index: 1;
  }

  h4 {
    font-family: 'Times New Roman', serif;
    font-size: 22px;
    font-weight: bold;
    letter-spacing: 2px;
    margin: 0;
    padding: 15px 20px;
    text-transform: uppercase;
    text-align: center;
    background-color: #fff;
    border-bottom: 3px solid #000;
    position: relative;
    z-index: 2;
    cursor: move;
    user-select: none;
  }
}

.modal-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: 2px solid #000;
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  z-index: 3;

  &:hover {
    background-color: #000;
    color: #fff;
  }
}

.modal-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background-color: #f5f5f5;
}

.settings-section {
  background-color: #fff;
  border: 2px solid #000;
  padding: 15px;
  margin-bottom: 15px;

  h5 {
    font-family: 'Times New Roman', serif;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
    margin: 0 0 12px 0;
    padding-bottom: 8px;
    border-bottom: 2px solid #000;
    text-transform: uppercase;
  }
}

.hint-text {
  font-size: 12px;
  color: #666;
  margin: 0 0 10px 0;
  font-style: italic;
}

.font-settings-controls {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 10px 15px;
  align-items: center;

  label {
    font-family: 'Times New Roman', serif;
    font-size: 13px;
    font-weight: bold;
  }

  input[type='color'] {
    width: 60px;
    height: 35px;
    border: 2px solid #000;
    cursor: pointer;
  }

  select,
  input[type='number'] {
    font-family: 'Courier New', monospace;
    font-size: 13px;
    padding: 6px 10px;
    border: 2px solid #000;
    background-color: #fff;
  }
}

.range-control {
  display: flex;
  align-items: center;
  gap: 10px;

  input[type='range'] {
    flex: 1;
  }

  .range-value {
    font-family: 'Courier New', monospace;
    font-size: 13px;
    font-weight: bold;
    min-width: 50px;
    text-align: right;
  }
}

.context-control-item {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;

  label {
    font-family: 'Times New Roman', serif;
    font-size: 13px;
    font-weight: bold;
  }

  input[type='checkbox'] {
    width: 18px;
    height: 18px;
    cursor: pointer;
  }

  input[type='text'],
  input[type='number'] {
    flex: 1;
    font-family: 'Courier New', monospace;
    font-size: 13px;
    padding: 6px 10px;
    border: 2px solid #000;
    background-color: #fff;
  }
}

.regex-rules-list {
  margin-bottom: 15px;
}

.regex-rule-item {
  background-color: #f9f9f9;
  border: 2px solid #000;
  padding: 12px;
  margin-bottom: 10px;

  .rule-header {
    display: flex;
    gap: 10px;
    margin-bottom: 8px;

    .rule-name-input {
      flex: 1;
      font-family: 'Times New Roman', serif;
      font-size: 14px;
      font-weight: bold;
      padding: 6px 10px;
      border: 2px solid #000;
      background-color: #fff;
    }

    .delete-rule-btn {
      width: 35px;
      height: 35px;
      border: 2px solid #dc3545;
      background-color: #fff;
      color: #dc3545;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s;

      &:hover {
        background-color: #dc3545;
        color: #fff;
      }
    }
  }

  .rule-content {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 8px;

    input {
      font-family: 'Courier New', monospace;
      font-size: 12px;
      padding: 6px 10px;
      border: 2px solid #000;
      background-color: #fff;
    }
  }

  .rule-options {
    display: flex;
    gap: 15px;

    label {
      display: flex;
      align-items: center;
      gap: 6px;
      font-size: 12px;
      cursor: pointer;

      input[type='checkbox'] {
        width: 16px;
        height: 16px;
      }
    }
  }
}

.empty-hint {
  text-align: center;
  padding: 30px;
  color: #999;
  font-style: italic;
  border: 2px dashed #ccc;
  background-color: #fafafa;
}

.rule-buttons-group {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;

  .major-action-button {
    flex: 1;
    min-width: 150px;
  }
}

.add-rule-btn {
  &:hover {
    background-color: #28a745 !important;
    border-color: #28a745 !important;
    color: #fff !important;
  }
}

.import-rules-btn {
  &:hover {
    background-color: #17a2b8 !important;
    border-color: #17a2b8 !important;
    color: #fff !important;
  }
}

.export-rules-btn {
  &:hover {
    background-color: #ffc107 !important;
    border-color: #ffc107 !important;
    color: #000 !important;
  }
}

.button-group {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.major-action-button {
  flex: 1;
  min-width: 150px;
  font-family: 'Times New Roman', serif;
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 1px;
  padding: 12px 18px;
  border: 3px solid #000;
  background-color: #fff;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  text-transform: uppercase;

  &:hover {
    background-color: #000;
    color: #fff;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }

  &:active {
    transform: translateY(0);
  }

  &.save-btn:hover {
    background-color: #28a745;
    border-color: #28a745;
    color: #fff;
  }

  i {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .settings-modal {
    width: 95%;
    max-width: 95%;
  }

  .font-settings-controls {
    grid-template-columns: 1fr;

    label {
      margin-top: 5px;
    }
  }

  .button-group {
    flex-direction: column;

    .major-action-button {
      width: 100%;
    }
  }
}
