
.step4-combat-stats {
  width: 100%;
  font-family: 'Times New Roman', serif;
}

// 
.stats-header {
  padding: 20px 30px;
  border-bottom: 2px solid #000;
  background-color: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;

  h3 {
    font-size: 20px;
    font-weight: bold;
  }

  .level-info {
    font-size: 16px;
    color: #666;
    padding: 5px 15px;
    border: 2px solid #666;
    background-color: #fff;
  }
}

// 
.stats-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  padding: 30px;
  background-color: #fff;

  @media (max-width: 968px) {
    grid-template-columns: 1fr;
  }

  // 
  .hitpoints-section {
    grid-column: 1 / -1;

    .hitpoints-card {
      border: 2px solid #000;
      padding: 30px;
      background-color: #f9f9f9;

      .roll-prompt {
        text-align: center;

        .prompt-text {
          font-size: 16px;
          margin-bottom: 20px;
          color: #333;
          line-height: 1.6;
        }

        .roll-button {
          padding: 15px 40px;
          font-size: 18px;
          font-weight: bold;
          background-color: #d9534f;
          color: #fff;
          border: 3px solid #c9302c;
          cursor: pointer;
          transition: all 0.3s;
          display: inline-flex;
          align-items: center;
          gap: 10px;

          &:hover {
            background-color: #c9302c;
            transform: translateY(-2px);
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
          }

          &:active {
            transform: translateY(0);
            box-shadow: none;
          }

          .dice-icon {
            font-size: 24px;
          }
        }
      }

      .hitpoints-result {
        .hp-display {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 20px;
          background-color: #fff;
          border: 3px solid #5cb85c;
          margin-bottom: 20px;

          .hp-total {
            display: flex;
            align-items: baseline;
            gap: 15px;

            .label {
              font-size: 20px;
              font-weight: bold;
            }

            .value {
              font-size: 36px;
              color: #d9534f;
              font-weight: bold;
            }
          }

          .reroll-button {
            padding: 10px 20px;
            font-size: 14px;
            font-weight: bold;
            background-color: #f0ad4e;
            color: #fff;
            border: 2px solid #eea236;
            cursor: pointer;
            transition: all 0.3s;
            display: inline-flex;
            align-items: center;
            gap: 8px;

            &:hover {
              background-color: #ec971f;
              transform: translateY(-1px);
            }

            .dice-icon {
              font-size: 16px;
            }
          }
        }

        .hp-breakdown {
          padding: 15px;
          background-color: #fff;
          border-left: 4px solid #5bc0de;
          margin-bottom: 15px;

          .breakdown-item {
            display: flex;
            justify-content: space-between;
            padding: 8px 0;
            border-bottom: 1px dotted #ccc;

            &:last-child {
              border-bottom: none;
            }

            .label {
              font-size: 16px;
              color: #666;
            }

            .value {
              font-size: 18px;
              font-weight: bold;

              &.positive {
                color: #5cb85c;
              }

              &.negative {
                color: #d9534f;
              }
            }
          }
        }

        .constitution-note {
          padding: 15px;
          background-color: #d9edf7;
          border-left: 4px solid #31b0d5;

          p {
            margin: 0;
            line-height: 1.6;
            font-size: 14px;

            strong {
              color: #31708f;
            }
          }
        }
      }
    }
  }
}

// 
.section-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 2px solid #000;
  display: flex;
  align-items: center;
  gap: 10px;

  .icon {
    font-size: 24px;
  }
}

// 
.movement-section {
  .movement-card {
    border: 2px solid #000;
    padding: 20px;
    background-color: #f9f9f9;

    .movement-item {
      display: flex;
      align-items: baseline;
      gap: 10px;
      padding: 12px;
      margin-bottom: 10px;
      background-color: #fff;
      border-left: 4px solid #5cb85c;

      &.primary {
        border-left-color: #5cb85c;
        font-size: 18px;
        font-weight: bold;
      }

      &.special {
        border-left-color: #5bc0de;
      }

      .label {
        font-weight: bold;
      }

      .value {
        font-size: 20px;
        color: #d9534f;
        font-weight: bold;
      }

      .hint {
        font-size: 14px;
        color: #666;
        font-style: italic;
      }
    }

    .movement-note {
      padding: 10px;
      background-color: #fffacd;
      border: 1px solid #f0ad4e;
      margin-top: 10px;
      display: flex;
      align-items: center;
      gap: 10px;

      .note-icon {
        font-size: 18px;
      }
    }

    .movement-explanation {
      margin-top: 20px;
      padding: 15px;
      background-color: #e8f5e9;
      border-left: 4px solid #4caf50;

      p {
        margin: 8px 0;
        line-height: 1.6;
        font-size: 14px;

        strong {
          color: #2e7d32;
        }
      }
    }
  }
}

// 
.combat-section {
  display: flex;
  flex-direction: column;
  gap: 30px;

  .thac0-section {
    .thac0-card {
      border: 2px solid #000;
      padding: 20px;
      background-color: #f9f9f9;
      display: flex;
      align-items: center;
      gap: 20px;

      .thac0-value {
        font-size: 72px;
        font-weight: bold;
        color: #d9534f;
        min-width: 120px;
        text-align: center;
        border-right: 2px solid #ccc;
        padding-right: 20px;
      }

      .thac0-description {
        flex: 1;

        p {
          margin: 8px 0;
          line-height: 1.6;
          font-size: 14px;
        }

        .category-note {
          margin-top: 15px;
          padding: 10px;
          background-color: #fff3cd;
          border-left: 4px solid #f0ad4e;

          .growth-rate {
            color: #856404;
            font-style: italic;
          }
        }
      }
    }
  }

  .saving-throws-section {
    .saving-throws-card {
      border: 2px solid #000;
      padding: 20px;
      background-color: #f9f9f9;

      .saves-table {
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 15px;
        background-color: #fff;

        th,
        td {
          border: 1px solid #ddd;
          padding: 12px;
        }

        th {
          background-color: #5bc0de;
          color: #fff;
          font-weight: bold;
          text-align: left;
        }

        .save-name {
          font-weight: bold;
        }

        .save-value {
          text-align: center;
          font-size: 18px;
          color: #d9534f;
          font-weight: bold;
        }

        tbody tr:nth-child(even) {
          background-color: #f9f9f9;
        }

        tbody tr:hover {
          background-color: #e8f5e9;
        }
      }

      .saves-explanation {
        padding: 15px;
        background-color: #e3f2fd;
        border-left: 4px solid #2196f3;

        p {
          margin: 0;
          line-height: 1.6;
          font-size: 14px;

          strong {
            color: #1565c0;
          }
        }
      }
    }
  }
}

// 
.bottom-actions {
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
  border-top: 2px solid #000;
  background-color: #f5f5f5;
  gap: 15px;

  button {
    display: flex;
    align-items: center;
    gap: 8px;

    .button-icon {
      font-size: 16px;
    }

    &.primary {
      margin-left: auto;
    }
  }
}

// 
@media (max-width: 768px) {
  .stats-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .stats-content {
    padding: 20px;
  }

  .thac0-card {
    flex-direction: column !important;

    .thac0-value {
      border-right: none !important;
      border-bottom: 2px solid #ccc;
      padding-right: 0 !important;
      padding-bottom: 20px;
    }
  }

  .bottom-actions {
    flex-wrap: wrap;
  }
}
