        }

        .subrace-english {
          font-size: 13px;
          color: #666;
          font-style: italic;
        }
      }

      .selected-indicator,
      .disabled-indicator {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 18px;
        font-weight: bold;
      }

      .selected-indicator {
        color: #5cb85c;
      }

      .disabled-indicator {
        color: #d9534f;
      }

      &.selected {
        background-color: #000;
        color: #fff;
        border-width: 3px;

        .subrace-english {
          color: #ccc;
