      }
    }
  }

  // 
  .subrace-details {
    flex: 1;
    overflow-y: auto;
    padding: 30px;
    background-color: #fff;

    .no-selection {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      color: #999;
      font-size: 18px;
    }

    .no-subrace-details,
    .subrace-details-content {
      max-width: 900px;
    }

    .no-subrace-details {
      h2 {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 10px;
      }

      .hint {
        font-size: 16px;
        color: #666;
        line-height: 1.6;
        margin-bottom: 20px;
      }

      .section-hint {
        color: #666;
        font-style: italic;
      }
    }

    .detail-header {
      margin-bottom: 20px;

      h2 {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 10px;
      }

      .description {
        font-size: 15px;
        line-height: 1.8;
        color: #333;
        text-align: justify;
      }
    }

    .divider {
      height: 2px;
      background-color: #000;
      margin: 20px 0;
    }

    .detail-section {
      margin-bottom: 25px;

      h4 {
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 12px;
        padding-bottom: 5px;
        border-bottom: 1px solid #ddd;
      }

      .ability-adjustments {
        p {
          margin-bottom: 10px;
        }

        .adjusted-preview {
          font-weight: bold;
          margin-top: 15px;
        }

        .adjusted-abilities {
          display: flex;
          gap: 15px;
          flex-wrap: wrap;

          span {
            padding: 5px 10px;
            border: 1px solid #ddd;

            &.adjusted {
              background-color: #fffacd;
              border-color: #000;
              font-weight: bold;

              .change {
                color: #d9534f;
                font-size: 12px;
              }
            }
          }
        }
      }

      .ability-requirements {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 10px;

        @media (max-width: 768px) {
          grid-template-columns: repeat(2, 1fr);
        }

        .ability-requirement {
          display: flex;
          align-items: center;
          gap: 8px;
          padding: 8px;
          border: 1px solid #ddd;

          .ability-label {
            font-weight: bold;
            min-width: 40px;
          }

          .requirement-value {
            flex: 1;
            color: #666;
          }

          .requirement-status {
            font-weight: bold;
            font-size: 16px;

            &.met {
              color: #5cb85c;
            }

            &.unmet {
              color: #d9534f;
            }
          }
        }
      }

      .infravision,
      .languages {
        line-height: 1.6;
      }

      .section-hint {
        font-size: 14px;
        color: #666;
        font-style: italic;
        margin-bottom: 12px;

        &.no-special-abilities {
          padding: 10px;
          background-color: #f5f5f5;
          border-left: 3px solid #999;
          margin-bottom: 0;
        }
      }

      .abilities-list {
        list-style: none;
        padding: 0;

        li {
          margin-bottom: 12px;
          line-height: 1.6;
          position: relative;
          padding-left: 15px;

          &::before {
            content: '';
            position: absolute;
            left: 0;
            color: #666;
            font-weight: bold;
          }

          .tooltip-icon {
            display: inline-block;
            width: 18px;
            height: 18px;
            line-height: 18px;
            text-align: center;
            border-radius: 50%;
            background-color: #666;
            color: #fff;
            font-size: 12px;
            cursor: help;
            margin: 0 3px;
          }

          .modified-badge,
          .new-badge {
            display: inline-block;
            padding: 2px 8px;
            margin-left: 8px;
            font-size: 11px;
            font-weight: bold;
            border-radius: 3px;
          }

          .modified-badge {
            background-color: #fff3cd;
            color: #856404;
            border: 1px solid #ffc107;
          }

          .new-badge {
            background-color: #d1ecf1;
            color: #0c5460;
            border: 1px solid #17a2b8;
          }
        }

        &.inherited {
          li {
            &::before {
              color: #4caf50;
            }
          }
        }

        &.subrace-specific {
          li {
            &::before {
              color: #2196f3;
            }
          }
        }
      }

      // 
      &.inherited-abilities-section {
        h4 {
          color: #2e7d32;
          border-bottom-color: #4caf50;
        }

        background-color: #f1f8f4;
        padding: 15px;
        border-left: 4px solid #4caf50;
        margin-left: -15px;
        margin-right: -15px;
        padding-left: 15px;
        padding-right: 15px;
      }

      // 
      &.subrace-specific-abilities-section {
        h4 {
          color: #1565c0;
          border-bottom-color: #2196f3;
        }

        background-color: #e3f2fd;
        padding: 15px;
        border-left: 4px solid #2196f3;
        margin-left: -15px;
        margin-right: -15px;
        padding-left: 15px;
        padding-right: 15px;
      }

      // /
      &.advantage-section {
        h4 {
          color: #2e7d32;
          border-bottom-color: #4caf50;
        }

        .advantage-text {
          padding: 12px;
          background-color: #e8f5e9;
          border-left: 4px solid #4caf50;
          line-height: 1.7;
        }
      }

      &.disadvantage-section {
        h4 {
          color: #c62828;
          border-bottom-color: #f44336;
        }

        .disadvantage-text {
          padding: 12px;
          background-color: #ffebee;
          border-left: 4px solid #f44336;
          line-height: 1.7;
        }
      }

      // 
      &.xp-penalty-section {
        h4 {
          color: #d84315;
          border-bottom-color: #ff5722;
        }

        .xp-penalty-text {
          font-size: 18px;
          font-weight: bold;
          color: #d84315;

          .xp-explanation {
            font-size: 14px;
            font-weight: normal;
            color: #666;
            margin-left: 8px;
          }
        }
      }
    }
  }
}

// ==========  ==========
.bottom-actions {
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
  border-top: 2px solid #000;
  background-color: #f5f5f5;
  gap: 15px;

  button {
    display: flex;
    align-items: center;
    gap: 8px;

    .button-icon {
      font-size: 16px;
    }

    &.primary {
      margin-left: auto;
    }
  }
}

// ==========  ==========
@media (max-width: 968px) {
  .race-selection .selection-content {
    flex-direction: column;

    .race-list {
      width: 100%;
      border-right: none;
      border-bottom: 2px solid #000;
      max-height: 300px;
    }
  }

  .subrace-selection .subrace-content-wrapper {
    flex-direction: column;

    .subrace-list {
      width: 100%;
      border-right: none;
      border-bottom: 2px solid #000;
      max-height: 250px;
    }

    .subrace-details {
      padding: 20px;
    }
  }
}

@media (max-width: 768px) {
  .category-selection {
    padding: 20px;
  }

  .race-selection .race-details {
    padding: 20px;
  }

  .subrace-selection .subrace-details {
    padding: 15px;

    .detail-section .ability-requirements {
      grid-template-columns: 1fr;
    }
  }

  .bottom-actions {
    flex-wrap: wrap;
  }
}
