
.settings-overlay {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2200;
  padding: 20px;
  overflow: auto;
}

.settings-modal {
  background-color: #f5f5dc;
  border: 4px solid #000;
  width: 90%;
  max-width: 700px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  position: relative;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 2px solid #666;
    pointer-events: none;
    z-index: 1;
  }

  h4 {
    font-family: 'Times New Roman', serif;
    font-size: 22px;
    font-weight: bold;
    letter-spacing: 2px;
    margin: 0;
    padding: 15px 20px;
    text-transform: uppercase;
    text-align: center;
    background-color: #fff;
    border-bottom: 3px solid #000;
    position: relative;
    z-index: 2;
  }
}

.modal-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: 2px solid #000;
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  z-index: 3;

  &:hover {
    background-color: #000;
    color: #fff;
  }
}

.modal-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background-color: #f5f5f5;
}

.settings-section {
  background-color: #fff;
  border: 2px solid #000;
  padding: 15px;
  margin-bottom: 15px;

  h5 {
    font-family: 'Times New Roman', serif;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
    margin: 0 0 12px 0;
    padding-bottom: 8px;
    border-bottom: 2px solid #000;
    text-transform: uppercase;
  }

  textarea {
    width: 100%;
    font-family: 'Courier New', monospace;
    font-size: 13px;
    padding: 10px;
    border: 2px solid #000;
    background-color: #f9f9f9;
    resize: vertical;

    &.summary-preview {
      background-color: #fffef5;
    }
  }

  label {
    font-family: 'Times New Roman', serif;
    font-size: 13px;
    font-weight: bold;
    display: block;
    margin-bottom: 6px;
  }

  input[type='text'],
  input[type='password'],
  input[type='number'] {
    width: 100%;
    font-family: 'Courier New', monospace;
    font-size: 13px;
    padding: 8px 10px;
    border: 2px solid #000;
    background-color: #fff;
    margin-bottom: 10px;
  }
}

.hint-text {
  font-size: 12px;
  color: #666;
  margin: 10px 0;
  font-style: italic;
  line-height: 1.5;
}

.api-source-controls {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 15px;

  label {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;

    input[type='radio'] {
      width: 18px;
      height: 18px;
    }
  }

  .segmented-btn {
    margin-left: auto;
  }
}

.custom-api-settings {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 2px dashed #ccc;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  margin-bottom: 15px;

  input[type='checkbox'] {
    width: 20px;
    height: 20px;
  }

  span {
    font-family: 'Times New Roman', serif;
    font-size: 14px;
    font-weight: bold;
  }
}

.auto-summary-controls,
.segmented-controls,
.range-controls {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.threshold-control {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.button-group {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.major-action-button {
  flex: 1;
  min-width: 150px;
  font-family: 'Times New Roman', serif;
  font-size: 13px;
  font-weight: bold;
  letter-spacing: 1px;
  padding: 12px 18px;
  border: 3px solid #000;
  background-color: #fff;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  text-transform: uppercase;

  &:hover:not(:disabled) {
    background-color: #000;
    color: #fff;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }

  &:active:not(:disabled) {
    transform: translateY(0);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &.save-btn:hover:not(:disabled) {
    background-color: #28a745;
    border-color: #28a745;
    color: #fff;
  }

  &.segmented-btn {
    flex: 0 0 auto;
    background-color: #e3f2fd;
    border-color: #2196f3;

    &:hover {
      background-color: #2196f3;
      border-color: #2196f3;
    }
  }

  i {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .settings-modal {
    width: 95%;
    max-width: 95%;
  }

  .api-source-controls {
    flex-direction: column;

    .segmented-btn {
      margin-left: 0;
    }
  }

  .button-group {
    flex-direction: column;

    .major-action-button {
      width: 100%;
    }
  }
}
