
.custom-race-creator {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.creator-header {
  text-align: center;
  margin-bottom: 30px;

  h2 {
    font-size: 28px;
    color: #8b4513;
    margin-bottom: 10px;
  }

  .rules-summary {
    color: #666;
    font-size: 14px;
    line-height: 1.6;
  }
}

.creator-content {
  background: #f9f7f4;
  padding: 20px;
  border: 2px solid #8b4513;
  border-radius: 8px;
}

.creator-section {
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px dashed #ccc;

  &:last-child {
    border-bottom: none;
  }

  h3 {
    font-size: 20px;
    color: #8b4513;
    margin-bottom: 15px;
  }

  .section-note {
    font-size: 13px;
    color: #666;
    margin-bottom: 15px;
    font-style: italic;
  }
}

.form-group {
  margin-bottom: 15px;

  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }

  input[type='text'],
  textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;

    &:focus {
      outline: none;
      border-color: #8b4513;
    }
  }
}

.size-options {
  display: flex;
  flex-direction: column;
  gap: 10px;

  .size-option {
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;

    input[type='radio'] {
      cursor: pointer;
    }

    .size-label {
      .size-desc {
        color: #666;
        font-size: 13px;
        margin-left: 8px;
      }
    }
  }
}

.size-adjustment-info {
  margin-top: 15px;
  padding: 10px;
  background: #fff3cd;
  border: 1px solid #ffc107;
  border-radius: 4px;
  color: #856404;
}

.ability-adjustments,
.ability-requirements {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
}

.adjustment-item,
.requirement-item {
  display: flex;
  align-items: center;
  gap: 10px;

  label {
    font-weight: bold;
    min-width: 60px;
  }

  select {
    flex: 1;
    padding: 6px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;

    &:disabled {
      background: #e9ecef;
      cursor: not-allowed;
    }
  }

  .min-max-inputs {
    display: flex;
    align-items: center;
    gap: 5px;
    flex: 1;

    input[type='number'] {
      width: 60px;
      padding: 6px;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 14px;
    }
  }
}

.adjustment-balance {
  margin-top: 15px;
  padding: 10px;
  border-radius: 4px;
  font-size: 14px;

  &.balanced {
    background: #d4edda;
    border: 1px solid #28a745;
    color: #155724;
  }

  &.unbalanced {
    background: #f8d7da;
    border: 1px solid #dc3545;
    color: #721c24;
  }
}

.class-limits {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;

  .class-item {
    label {
      display: flex;
      align-items: center;
      gap: 8px;
      cursor: pointer;

      input[type='checkbox'] {
        cursor: pointer;
      }
    }
  }
}

.abilities-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.ability-item {
  background: white;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 4px;

  .ability-inputs {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;

    input[type='text'] {
      flex: 1;
      padding: 6px;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 14px;
    }
  }

  .ability-tooltip {
    label {
      display: flex;
      align-items: center;
      gap: 8px;
      margin-bottom: 8px;
      cursor: pointer;

      input[type='checkbox'] {
        cursor: pointer;
      }
    }

    textarea {
      width: 100%;
      padding: 6px;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 13px;
    }
  }
}

.languages-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.language-item {
  display: flex;
  gap: 10px;

  input[type='text'] {
    flex: 1;
    padding: 6px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
  }
}

.btn-add {
  padding: 8px 16px;
  background: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;

  &:hover {
    background: #218838;
  }
}

.btn-remove {
  padding: 6px 12px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;

  &:hover {
    background: #c82333;
  }
}

.creator-footer {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;

  button {
    padding: 12px 30px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;

    &.btn-primary {
      background: #8b4513;
      color: white;

      &:hover:not(:disabled) {
        background: #6d3410;
      }

      &:disabled {
        background: #ccc;
        cursor: not-allowed;
      }
    }

    &.btn-secondary {
      background: #6c757d;
      color: white;

      &:hover {
        background: #5a6268;
      }
    }
  }
}

.validation-errors {
  margin-top: 20px;
  padding: 15px;
  background: #f8d7da;
  border: 1px solid #dc3545;
  border-radius: 4px;
  color: #721c24;

  h4 {
    margin-bottom: 10px;
  }

  ul {
    margin: 0;
    padding-left: 20px;

    li {
      margin-bottom: 5px;
    }
  }
}

@media (max-width: 768px) {
  .ability-adjustments,
  .ability-requirements {
    grid-template-columns: 1fr;
  }

  .class-limits {
    grid-template-columns: repeat(2, 1fr);
  }
}
