
// ADND 2E 
.adnd-status-panel {
  width: 280px;
  height: 100%;
  background-color: #fff;
  border-right: 4px solid #000;
  display: flex;
  flex-direction: column;
  font-family: 'Times New Roman', serif;
  color: #000;
  box-shadow: inset 0 0 0 2px #666;

  &.in-modal {
    width: 100%;
    height: auto;
    border: 3px solid #000;
    box-shadow: none;
  }
}

.panel-tabs {
  display: flex;
  background-color: #fff;
  border-bottom: 3px solid #000;
  overflow-x: auto;

  .tab-item {
    flex: 1;
    padding: 10px 8px;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s;
    border-right: 2px solid #000;
    font-size: 11px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    background-color: #fff;
    color: #000;
    white-space: nowrap;

    &:last-child {
      border-right: none;
    }

    &:hover {
      background-color: #000;
      color: #fff;
    }

    &.active {
      background-color: #000;
      color: #fff;
      box-shadow: inset 0 0 0 2px #666;
    }

    i {
      display: block;
      margin-bottom: 4px;
      font-size: 14px;
    }
  }
}

.panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 15px;
  background-color: #f5f5f5;
  background-image: repeating-linear-gradient(
    0deg,
    transparent,
    transparent 1px,
    rgba(0, 0, 0, 0.03) 1px,
    rgba(0, 0, 0, 0.03) 2px
  );
}

.tab-pane {
  animation: fadeIn 0.3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.info-row {
  display: flex;
  justify-content: space-between;
  padding: 8px 12px;
  border-bottom: 2px solid #000;
  background-color: #fff;
  margin-bottom: 2px;

  .label {
    color: #000;
    font-size: 13px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.5px;
  }

  .value {
    color: #000;
    font-weight: bold;
    font-size: 14px;
    font-family: 'Courier New', monospace;
  }
}

.attributes-pane {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.ability-section {
  background-color: #fff;
  border: 3px solid #000;
  position: relative;
  transition: all 0.3s ease;

  &::before {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    border: 1px solid #666;
    pointer-events: none;
  }

  &.collapsed {
    .ability-header {
      border-bottom: none;
    }
  }
}

.ability-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  background-color: #000;
  color: #fff;
  border-bottom: 2px solid #000;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #333;
  }

  .collapse-icon {
    font-size: 12px;
    margin-left: 8px;
    user-select: none;
  }
}

.ability-name {
  font-family: 'Courier New', monospace;
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.ability-score {
  font-family: 'Courier New', monospace;
  font-size: 24px;
  font-weight: bold;
  min-width: 40px;
  text-align: right;
}

.ability-modifiers {
  padding: 8px 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.modifier-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 6px;
  font-family: 'Courier New', monospace;
  font-size: 11px;
  border-bottom: 1px dashed #ddd;

  &:last-child {
    border-bottom: none;
  }
}

.mod-label {
  font-weight: bold;
  color: #333;
}

.mod-value {
  font-weight: bold;
  color: #000;
  text-align: right;
}

.section-title {
  font-size: 13px;
  font-weight: bold;
  color: #000;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: 15px 0 10px 0;
  padding: 8px 12px;
  border: 2px solid #000;
  background-color: #fff;
  box-shadow: 3px 3px 0 #000;
  position: relative;

  &.clickable {
    cursor: pointer;
    transition: background-color 0.2s;
    user-select: none;

    &:hover {
      background-color: #f0f0f0;
    }

    .collapse-icon {
      position: absolute;
      right: 12px;
      top: 50%;
      transform: translateY(-50%);
      font-size: 12px;
    }
  }

  .skill-note {
    font-size: 10px;
    font-weight: normal;
    color: #666;
    margin-left: 8px;
    font-style: italic;
  }
}

.collapsible-section {
  transition: all 0.3s ease;

  &.collapsed {
    .section-title {
      margin-bottom: 15px;
    }
  }
}

.skill-list,
.equipment-list {
  display: flex;
  flex-direction: column;
  gap: 2px;

  .skill-item,
  .equipment-item {
    background-color: #fff;
    padding: 8px 12px;
    border: 2px solid #000;
    border-left: 4px solid #000;
    font-size: 13px;
    font-family: 'Courier New', monospace;
    position: relative;

    &.clickable-skill {
      cursor: pointer;
      transition: all 0.2s;

      &:hover {
        background-color: #f8f8f8;
        border-left-color: #4a90e2;
        transform: translateX(2px);
      }

      .info-icon {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 14px;
        opacity: 0.6;
      }
    }
  }
}

.ability-list {
  display: flex;
  flex-direction: column;
  gap: 8px;

  .ability-item {
    background-color: #fff;
    padding: 10px;
    border: 3px solid #000;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 3px;
      left: 3px;
      right: 3px;
      bottom: 3px;
      border: 1px solid #666;
      pointer-events: none;
    }

    &.clickable-ability {
      cursor: pointer;
      transition: all 0.2s;

      &:hover {
        background-color: #f8f8f8;
        border-left-width: 6px;
        border-left-color: #4a90e2;
        transform: translateX(2px);
      }

      .ability-name {
        .info-icon {
          font-size: 14px;
          opacity: 0.6;
          margin-left: 8px;
        }
      }
    }

    .ability-name {
      font-weight: bold;
      color: #000;
      margin-bottom: 6px;
      font-size: 12px;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    .ability-desc {
      color: #333;
      font-size: 11px;
      line-height: 1.5;
    }
  }
}

.empty-text {
  color: #999;
  font-style: italic;
  padding: 10px;
  text-align: center;
  background-color: #fff;
  border: 2px dashed #ccc;
}

// 
.weapon-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.weapon-detail {
  background-color: #fff;
  border: 3px solid #000;
  position: relative;
  transition: all 0.3s ease;

  &::before {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    border: 1px solid #666;
    pointer-events: none;
  }

  &.collapsed {
    .weapon-header {
      border-bottom: none;
    }
  }
}

.weapon-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  background-color: #000;
  color: #fff;
  border-bottom: 2px solid #000;
  position: relative;

  &.clickable {
    cursor: pointer;
    transition: background-color 0.2s;

    &:hover {
      background-color: #333;
    }
  }

  .weapon-name {
    font-family: 'Courier New', monospace;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    flex: 1;
  }

  .weapon-quantity {
    font-family: 'Courier New', monospace;
    font-size: 12px;
    font-weight: bold;
    background-color: #fff;
    color: #000;
    padding: 2px 8px;
    border: 2px solid #000;
    margin-right: 8px;
  }

  .collapse-icon {
    font-size: 12px;
    margin-left: 8px;
    user-select: none;
  }
}

.weapon-stats {
  padding: 10px 12px;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.weapon-stat-row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 4px 6px;
  font-family: 'Courier New', monospace;
  font-size: 11px;
  border-bottom: 1px dashed #ddd;
  gap: 8px;

  &:last-child {
    border-bottom: none;
  }

  .stat-label {
    font-weight: bold;
    color: #333;
    min-width: 80px;
    flex-shrink: 0;
  }

  .stat-value {
    font-weight: bold;
    color: #000;
    text-align: right;
    flex: 1;
    word-break: break-word;
  }
}

// 
.ammunition-row {
  background-color: #fffacd;
  border: 1px solid #ffd700;
  padding: 6px;
  margin: 2px 0;

  .stat-label {
    color: #8b7500;
  }

  .stat-value {
    color: #8b7500;
  }
}

// 
.thief-skills-section {
  margin-top: 20px;

  .section-title {
    position: relative;

    .skill-note {
      font-size: 10px;
      font-weight: normal;
      color: #666;
      margin-left: 8px;
      font-style: italic;
    }
  }
}

.thief-skills-grid {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.thief-skill-item {
  background-color: #fff;
  border: 3px solid #000;
  padding: 10px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
    border: 1px solid #666;
    pointer-events: none;
  }

  .skill-name-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 6px;
    padding-bottom: 6px;
    border-bottom: 2px solid #000;

    .skill-name {
      font-family: 'Courier New', monospace;
      font-size: 13px;
      font-weight: bold;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      color: #000;
    }

    .skill-value {
      font-family: 'Courier New', monospace;
      font-size: 16px;
      font-weight: bold;
      color: #000;
      background-color: #f0f0f0;
      padding: 2px 8px;
      border: 2px solid #000;
    }
  }

  .skill-description {
    font-size: 11px;
    line-height: 1.4;
    color: #333;
    margin-bottom: 6px;
  }

  .skill-breakdown {
    font-family: 'Courier New', monospace;
    font-size: 10px;
    color: #666;
    font-style: italic;
    padding-top: 4px;
    border-top: 1px dashed #ddd;
  }
}

// 
.proficiency-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.proficiency-modal-content {
  background-color: #f5e6d3;
  border: 4px solid #000;
  box-shadow: 8px 8px 0 rgba(0, 0, 0, 0.5);
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
  animation: slideIn 0.3s ease;

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 2px solid #666;
    pointer-events: none;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  background-color: #000;
  color: #fff;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 3px solid #000;

  h2 {
    margin: 0;
    font-size: 18px;
    font-family: 'Courier New', monospace;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  .close-button {
    background: none;
    border: 2px solid #fff;
    color: #fff;
    font-size: 20px;
    width: 32px;
    height: 32px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s;

    &:hover {
      background-color: #fff;
      color: #000;
    }
  }
}

.modal-body {
  padding: 20px;
  overflow-y: auto;
  flex: 1;
}

.prof-info-row {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  padding: 8px 12px;
  background-color: #fff;
  border: 2px solid #000;
  border-left: 4px solid #000;

  .prof-label {
    font-weight: bold;
    font-size: 13px;
    font-family: 'Courier New', monospace;
    min-width: 100px;
    color: #000;
  }

  .prof-value {
    font-size: 13px;
    font-family: 'Courier New', monospace;
    color: #333;
  }
}

.prof-description {
  margin-top: 20px;
  padding: 15px;
  background-color: #fff;
  border: 3px solid #000;
  box-shadow: 3px 3px 0 #000;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    border: 1px solid #666;
    pointer-events: none;
  }

  h3 {
    margin: 0 0 12px 0;
    font-size: 14px;
    font-family: 'Courier New', monospace;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #000;
    border-bottom: 2px solid #000;
    padding-bottom: 8px;
  }

  p {
    margin: 0;
    font-size: 13px;
    line-height: 1.6;
    color: #333;
    white-space: pre-wrap;
    font-family: 'Arial', sans-serif;
  }
}

// ====================  ====================
.turn-undead-modal {
  .turn-undead-content {
    max-width: 900px;
    width: 95%;
  }

  .turn-undead-body {
    overflow-y: auto;
    max-height: 70vh;
  }

  .turn-section {
    margin-bottom: 24px;
    padding: 15px;
    background-color: #fff;
    border: 3px solid #000;
    box-shadow: 3px 3px 0 #000;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 4px;
      left: 4px;
      right: 4px;
      bottom: 4px;
      border: 1px solid #666;
      pointer-events: none;
    }

    h3 {
      margin: 0 0 15px 0;
      font-size: 16px;
      font-family: 'Courier New', monospace;
      text-transform: uppercase;
      letter-spacing: 1px;
      color: #000;
      border-bottom: 2px solid #000;
      padding-bottom: 8px;
    }

    p {
      font-size: 13px;
      line-height: 1.7;
      color: #333;
      margin: 10px 0;
      font-family: 'Arial', sans-serif;
    }

    ul {
      margin: 10px 0;
      padding-left: 25px;
      font-size: 13px;
      line-height: 1.7;
      color: #333;
      font-family: 'Arial', sans-serif;

      li {
        margin: 8px 0;
      }
    }
  }

  .turn-example {
    background-color: #fffacd;

    p {
      font-style: italic;
    }
  }

  .turn-table-wrapper {
    overflow-x: auto;
    margin: 15px 0;
  }

  .turn-undead-table {
    width: 100%;
    border-collapse: collapse;
    background-color: #fff;
    font-family: 'Courier New', monospace;
    font-size: 12px;

    thead {
      background-color: #000;
      color: #fff;

      th {
        padding: 8px;
        border: 2px solid #000;
        font-weight: bold;
        text-align: center;
        white-space: nowrap;
      }
    }

    tbody {
      td {
        padding: 6px 8px;
        border: 2px solid #000;
        text-align: center;
        font-weight: bold;

        &.undead-name {
          text-align: left;
          background-color: #f5f5f5;
          white-space: nowrap;
        }

        &.result-auto-turn {
          background-color: #90ee90;
          color: #000;
        }

        &.result-auto-destroy {
          background-color: #ffd700;
          color: #000;
        }

        &.result-auto-destroy-plus {
          background-color: #ff6347;
          color: #fff;
        }

        &.result-impossible {
          background-color: #ddd;
          color: #999;
        }

        &.current-level {
          outline: 3px solid #1e90ff;
          outline-offset: -3px;
          box-shadow: inset 0 0 8px rgba(30, 144, 255, 0.4);
        }
      }
    }

    tr:nth-child(even) {
      td:not(.undead-name):not(.result-auto-turn):not(.result-auto-destroy):not(.result-auto-destroy-plus):not(
          .result-impossible
        ) {
        background-color: #f9f9f9;
      }
    }
  }

  .turn-notes {
    margin-top: 15px;
    padding: 10px;
    background-color: #fffacd;
    border: 2px solid #daa520;
    border-radius: 4px;

    .note-item {
      font-size: 11px;
      line-height: 1.5;
      color: #666;
      margin: 5px 0;
      font-style: italic;

      &::before {
        content: ' ';
        color: #daa520;
      }
    }
  }
}
