
.equipment-purchase {
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

// 
.dice-dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.dice-dialog {
  background-color: #fff;
  border: 4px solid #000;
  min-width: 500px;
  max-width: 600px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);

  &-header {
    padding: 25px 30px;
    border-bottom: 3px solid #000;
    background-color: #f5f5f5;
  }

  &-title {
    font-family: 'Times New Roman', serif;
    font-size: 26px;
    font-weight: bold;
    margin: 0;
    text-align: center;
  }

  &-body {
    padding: 35px 30px;
  }

  &-footer {
    padding: 20px 30px;
    border-top: 3px solid #000;
    background-color: #f5f5f5;
    display: flex;
    justify-content: center;
    gap: 15px;
  }
}

.dice-instruction {
  font-family: 'Times New Roman', serif;
  font-size: 16px;
  line-height: 1.6;
  margin: 0 0 25px 0;
  text-align: center;

  strong {
    color: #c00;
    font-weight: bold;
  }
}

.dice-result-area {
  min-height: 120px;
  padding: 25px;
  border: 3px solid #000;
  background-color: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
}

.roll-prompt {
  font-family: 'Times New Roman', serif;
  font-size: 18px;
  color: #666;
  font-style: italic;
  text-align: center;

  p {
    margin: 0;
  }
}

.roll-result {
  width: 100%;
}

.roll-details {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  font-family: 'Courier New', monospace;
  font-size: 18px;
}

.roll-label {
  font-weight: bold;
}

.roll-values {
  color: #666;
}

.roll-total {
  font-weight: bold;
  color: #000;
  font-size: 20px;
}

.money-result {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 15px;
  background-color: #fff;
  border: 2px solid #000;
  font-family: 'Times New Roman', serif;
  font-size: 20px;
}

.money-label {
  font-weight: bold;
}

.money-value {
  font-family: 'Courier New', monospace;
  font-size: 28px;
  font-weight: bold;
  color: #0a0;
}

.dice-roll-button,
.dice-reroll-button,
.dice-confirm-button {
  padding: 14px 35px;
  border: 3px solid #000;
  background-color: #fff;
  font-family: 'Times New Roman', serif;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;
  text-transform: uppercase;
  letter-spacing: 1px;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
}

.dice-roll-button {
  background-color: #0a0;
  color: #fff;
  border-color: #0a0;
  min-width: 150px;

  &:hover {
    background-color: #0c0;
    border-color: #0c0;
  }
}

.dice-reroll-button {
  background-color: #f90;
  color: #fff;
  border-color: #f90;

  &:hover {
    background-color: #fa0;
    border-color: #fa0;
  }
}

.dice-confirm-button {
  background-color: #00a;
  color: #fff;
  border-color: #00a;

  &:hover {
    background-color: #00c;
    border-color: #00c;
  }
}

// 
.money-info {
  display: flex;
  gap: 25px;
  flex-wrap: wrap;
}

.info-card {
  flex: 1;
  min-width: 160px;
  padding: 20px 25px;
  background-color: #f5f5f5;
  border: 3px solid #000;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
    border: 1px solid #666;
    pointer-events: none;
  }
}

.info-label {
  font-family: 'Times New Roman', serif;
  font-size: 15px;
  color: #666;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.info-value {
  font-family: 'Courier New', monospace;
  font-size: 26px;
  font-weight: bold;
  color: #000;

  &.spent {
    color: #c00;
  }

  &.remaining {
    color: #0a0;
  }
}

// 
.main-content {
  display: flex;
  gap: 30px;
  flex: 1;

  @media (max-width: 1200px) {
    flex-direction: column;
  }
}

// 
.categories-panel {
  width: 220px;
  border: 3px solid #000;
  padding: 20px;
  background-color: #fff;
  overflow-y: auto;

  @media (max-width: 1200px) {
    width: 100%;
    max-height: 300px;
  }
}

.panel-title {
  font-family: 'Times New Roman', serif;
  font-size: 19px;
  font-weight: bold;
  margin: 0 0 20px 0;
  padding-bottom: 12px;
  border-bottom: 2px solid #000;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.category-item {
  padding: 12px 14px;
  margin-bottom: 8px;
  border: 2px solid #999;
  background-color: #f5f5f5;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &:hover {
    background-color: #e0e0e0;
    border-color: #000;
  }

  &.active {
    background-color: #000;
    color: #fff;
    border-color: #000;

    .category-count {
      color: #ccc;
    }
  }
}

.category-name {
  font-family: 'Times New Roman', serif;
  font-size: 15px;
}

.category-count {
  font-size: 13px;
  color: #666;
}

// 
.equipment-list-panel {
  flex: 2;
  border: 3px solid #000;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}

.panel-header {
  padding: 15px 20px;
  border-bottom: 2px solid #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}

.search-box {
  flex: 1;
  min-width: 200px;
  max-width: 300px;
}

.search-input {
  width: 100%;
  padding: 8px 12px;
  border: 2px solid #000;
  font-family: 'Times New Roman', serif;
  font-size: 14px;

  &:focus {
    outline: none;
    border-color: #000;
    background-color: #f9f9f9;
  }
}

.equipment-table-container {
  flex: 1;
  overflow-y: auto;
  padding: 25px;
}

.equipment-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 8px;
  font-family: 'Times New Roman', serif;

  thead {
    background-color: #f5f5f5;
    position: sticky;
    top: 0;
    z-index: 1;
  }

  th {
    padding: 15px;
    text-align: left;
    border: 2px solid #000;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 14px;
    letter-spacing: 1px;
    background-color: #f5f5f5;
  }

  td {
    padding: 15px;
    border: 1px solid #ccc;
    background-color: #fff;
  }

  .equipment-row {
    transition: background-color 0.2s;

    &:hover td {
      background-color: #f9f9f9;
    }
  }

  .name-col {
    min-width: 220px;
  }

  .equipment-name-wrapper {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  .equipment-name {
    font-weight: bold;
    font-size: 15px;
  }

  .name-en {
    font-size: 13px;
    color: #666;
    font-style: italic;
  }

  .equipment-desc {
    font-size: 12px;
    color: #999;
    font-style: italic;
  }

  .price-col,
  .weight-col {
    font-family: 'Courier New', monospace;
    white-space: nowrap;
  }

  .quantity-col {
    width: 100px;
    text-align: center;
  }

  .action-col {
    width: 100px;
    text-align: center;
  }

  .no-data {
    text-align: center;
    padding: 40px;
    color: #999;
    font-style: italic;
  }
}

.quantity-input {
  width: 60px;
  padding: 5px;
  border: 2px solid #000;
  text-align: center;
  font-family: 'Courier New', monospace;

  &:focus {
    outline: none;
    background-color: #f9f9f9;
  }
}

.add-button {
  padding: 6px 16px;
  border: 2px solid #000;
  background-color: #fff;
  font-family: 'Times New Roman', serif;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;

  &:hover:not(:disabled) {
    background-color: #000;
    color: #fff;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

// 
.pagination {
  padding: 15px 20px;
  border-top: 2px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.page-button {
  padding: 8px 16px;
  border: 2px solid #000;
  background-color: #fff;
  font-family: 'Times New Roman', serif;
  cursor: pointer;
  transition: all 0.2s;

  &:hover:not(:disabled) {
    background-color: #000;
    color: #fff;
  }

  &:disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
}

.page-info {
  font-family: 'Times New Roman', serif;
  font-size: 14px;
}

// 
.cart-panel {
  width: 340px;
  border: 3px solid #000;
  padding: 20px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  overflow-y: auto;

  @media (max-width: 1200px) {
    width: 100%;
    max-height: 400px;
  }
}

.empty-cart {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
}

.empty-icon {
  font-size: 64px;
  margin-bottom: 15px;
  opacity: 0.3;
}

.empty-text {
  font-family: 'Times New Roman', serif;
  font-size: 16px;
  color: #999;
  font-style: italic;
}

.cart-items {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 18px;
  overflow-y: auto;
}

.cart-item {
  padding: 16px;
  border: 2px solid #000;
  background-color: #f9f9f9;
}

.cart-item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.cart-item-name {
  font-family: 'Times New Roman', serif;
  font-weight: bold;
  font-size: 15px;
}

.remove-button {
  width: 24px;
  height: 24px;
  border: 2px solid #c00;
  background-color: #fff;
  color: #c00;
  font-size: 18px;
  line-height: 1;
  cursor: pointer;
  transition: all 0.2s;
  font-weight: bold;

  &:hover {
    background-color: #c00;
    color: #fff;
  }
}

.cart-item-details {
  font-family: 'Times New Roman', serif;
  font-size: 12px;
  color: #666;
}

.cart-item-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.cart-quantity-control {
  display: flex;
  gap: 5px;
  align-items: center;
}

.quantity-btn {
  width: 24px;
  height: 24px;
  border: 2px solid #000;
  background-color: #fff;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.2s;

  &:hover:not(:disabled) {
    background-color: #000;
    color: #fff;
  }

  &:disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
}

.cart-quantity-input {
  width: 50px;
  padding: 3px;
  border: 2px solid #000;
  text-align: center;
  font-family: 'Courier New', monospace;
  font-size: 12px;

  &:focus {
    outline: none;
    background-color: #fff;
  }
}

.cart-item-total {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid #ccc;
  font-weight: bold;
  color: #000;
}

.cart-summary {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 2px solid #000;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  font-family: 'Times New Roman', serif;
  font-size: 14px;
}

.summary-value {
  font-weight: bold;
  font-family: 'Courier New', monospace;
}

.clear-cart-button {
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  border: 2px solid #c00;
  background-color: #fff;
  color: #c00;
  font-family: 'Times New Roman', serif;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background-color: #c00;
    color: #fff;
  }
}

// 
.navigation-buttons {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  padding-top: 20px;
  border-top: 3px solid #000;
}

.adnd-button {
  padding: 12px 24px;
  border: 3px solid #000;
  background-color: #fff;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s;
  text-transform: uppercase;
  font-family: 'Times New Roman', serif;
  letter-spacing: 1px;

  &:hover:not(:disabled) {
    background-color: #f0f0f0;
    transform: translateY(-2px);
  }

  &.primary {
    background-color: #000;
    color: #fff;

    &:hover:not(:disabled) {
      background-color: #333;
    }
  }

  &.secondary {
    background-color: #666;
    color: #fff;
    border-color: #666;

    &:hover:not(:disabled) {
      background-color: #888;
    }
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}
