
.floating-container {
  position: fixed;
  z-index: 1000;
}

.floating-button {
  position: fixed;
  bottom: 20px;
  left: 20px;
  width: 50px;
  height: 50px;
  background-color: #fff;
  border: 3px solid #000;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 1002;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);

  &:hover {
    background-color: #000;
    transform: scale(1.1);
    box-shadow: 3px 3px 12px rgba(0, 0, 0, 0.3);

    .icon {
      color: #fff;
    }
  }

  &:active {
    transform: scale(1.05);
  }
}

.icon {
  font-size: 24px;
  color: #000;
  transition: all 0.3s ease;

  &.rotating {
    animation: rotate 2s linear infinite;
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.settings-panel {
  position: fixed;
  bottom: 80px;
  left: 20px;
  width: 380px;
  max-height: calc(100vh - 120px);
  background-color: #fff;
  border: 3px solid #000;
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.3);
  z-index: 1001;
  display: flex;
  flex-direction: column;

  @media (max-width: 480px) {
    width: calc(100vw - 40px);
    left: 20px;
    right: 20px;
    max-height: calc(100vh - 100px);
  }
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 2px solid #000;
  background-color: #f5f5f5;

  h3 {
    font-family: 'Times New Roman', serif;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin: 0;
  }
}

.close-button {
  background: none;
  border: none;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.2s ease;

  &:hover {
    transform: scale(1.2);
  }
}

.panel-content {
  padding: 20px;
  overflow-y: auto;
  flex: 1;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: #f5f5f5;
  }

  &::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 4px;

    &:hover {
      background: #999;
    }
  }
}

// 
.cache-section {
  margin-bottom: 25px;
  padding-bottom: 20px;
  border-bottom: 2px dashed #ddd;

  &:last-child {
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 0;
  }
}

.section-title {
  font-family: 'Times New Roman', serif;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin: 0 0 15px 0;
  color: #333;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.clear-all-button {
  padding: 4px 12px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 13px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    background: #c82333;
    transform: translateY(-1px);
  }

  &:active {
    transform: translateY(0);
  }
}

.cache-item {
  margin-bottom: 10px;
}

.cache-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  font-family: 'Courier New', monospace;
}

.cache-label {
  font-weight: bold;
  font-size: 14px;
}

.cache-size {
  font-size: 14px;
  color: #666;
  font-weight: bold;
}

.delete-button {
  width: 100%;

  &.danger {
    background: #dc3545;
    border-color: #dc3545;

    &:hover {
      background: #c82333;
    }
  }
}

.section-note {
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #fff3cd;
  border: 1px solid #ffc107;
  border-radius: 4px;

  p {
    font-size: 12px;
    line-height: 1.4;
    color: #856404;
    margin: 0;
  }
}

// 
.empty-state {
  text-align: center;
  padding: 20px;
  color: #999;
  font-size: 14px;

  p {
    margin: 0;
  }
}

// 
.custom-races-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.race-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  transition: all 0.2s ease;

  &:hover {
    background: #f5f5f5;
    border-color: #999;
  }
}

.race-info {
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1;
}

.race-icon {
  font-size: 24px;
  line-height: 1;
}

.race-details {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.race-name {
  font-weight: bold;
  font-size: 14px;
  color: #333;
}

.race-english {
  font-size: 12px;
  color: #666;
  font-style: italic;
}

.delete-race-button {
  width: 28px;
  height: 28px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease;
  flex-shrink: 0;

  &:hover {
    background: #c82333;
    transform: scale(1.1);
  }

  &:active {
    transform: scale(1.05);
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1000;
}

// 
.slide-fade-enter-active {
  transition: all 0.3s ease;
}

.slide-fade-leave-active {
  transition: all 0.2s ease;
}

.slide-fade-enter-from {
  transform: translateY(20px);
  opacity: 0;
}

.slide-fade-leave-to {
  transform: translateY(10px);
  opacity: 0;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
