// ==================== GPU  ====================

//  GPU 
.gpu-accelerated {
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
  will-change: transform;
}

// 
.will-change-transform {
  will-change: transform;
}

.will-change-opacity {
  will-change: opacity;
}

//  will-change JS 
.will-change-auto {
  will-change: auto;
}

// ==================== CSS Containment ====================

// 
.contain-layout {
  contain: layout;
}

// 
.contain-style {
  contain: style;
}

// 
.contain-paint {
  contain: paint;
  overflow: hidden; // paint containment  overflow
}

// 
.contain-size {
  contain: size;
}

// layout + style + paint
.contain-strict {
  contain: strict; // layout + style + paint + size
}

// layout + style + paint
.contain-content {
  contain: content; // layout + style + paint
}

// ====================  ====================

// 
.virtual-scroll-container {
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
  contain: strict; // 
  will-change: scroll-position;

  // 
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: #f5f5f5;
  }

  &::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 4px;

    &:hover {
      background: #999;
    }
  }
}

//  transform  top/left
.virtual-scroll-item {
  position: absolute;
  left: 0;
  right: 0;
  contain: content; // 
  will-change: transform; // GPU 

  //  transform  top
  transform: translateY(var(--item-offset, 0));
}

// ====================  ====================

// 
.optimized-list-item {
  contain: content;
  display: block;
  min-height: 1px; //  size containment 

  // 
  padding: 1px 0;
}

// 
.optimized-card {
  contain: content;
  display: block;
  position: relative;

  //  transform  margin 
  &:not(:last-child) {
    margin-bottom: 0;
    transform: translateY(0);
  }
}

// ====================  ====================

//  transform  opacity GPU 
@keyframes fade-in-optimized {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fade-out-optimized {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.95);
  }
}

@keyframes slide-in-optimized {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

// 
.fade-in-fast {
  animation: fade-in-optimized 0.2s ease-out forwards;
  will-change: opacity, transform;
}

// 
.fade-out-fast {
  animation: fade-out-optimized 0.2s ease-in forwards;
  will-change: opacity, transform;
}

// ====================  ====================

// 
.fixed-size-container {
  //  aspect-ratio 
  min-height: var(--min-height, 100px);

  // 
  overflow: hidden;
  contain: layout size;
}

// 
.skeleton-container {
  position: relative;
  overflow: hidden;
  background: linear-gradient(90deg, #f0f0f0 0%, #e0e0e0 50%, #f0f0f0 100%);
  background-size: 200% 100%;
  animation: skeleton-loading 1.5s ease-in-out infinite;
  contain: paint;
}

@keyframes skeleton-loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

// ====================  ====================

//  transform  position 
.transform-position {
  position: relative;
  //  top/left/right/bottom
  //  transform: translate()
  transform: translate(var(--x, 0), var(--y, 0));
  will-change: transform;
}

//  transform  width/height 
.transform-scale {
  transform: scale(var(--scale, 1));
  transform-origin: center;
  will-change: transform;
}

// ====================  ====================

// 
.display-none-optimized {
  display: none !important;
  //  content-visibility: hidden ()
  // content-visibility: hidden;
}

// 
.lazy-placeholder {
  min-height: var(--placeholder-height, 200px);
  background: #f5f5f5;
  contain: strict;
}

// ====================  ====================

// 
.no-tap-delay {
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

// 
.smooth-scroll {
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
  overscroll-behavior: contain;
}

// ====================  ====================

// 
.optimized-text {
  // 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  // 
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Helvetica Neue',
    Arial,
    sans-serif;

  // 
  font-variant-ligatures: none;

  // 
  text-rendering: optimizeSpeed;
}

// 
.long-text-optimized {
  //  CSS  JS
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;

  // 
  &.single-line {
    display: block;
    -webkit-line-clamp: unset;
    -webkit-box-orient: unset;
    white-space: nowrap;
  }
}

// ====================  ====================

// 
.content-visibility-auto {
  content-visibility: auto;
  contain-intrinsic-size: var(--intrinsic-size, auto 500px);
}

// 
.force-hardware-acceleration {
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}

// 
.isolate-layer {
  isolation: isolate;
}

// 
.no-select {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
