
.step11-character-sheet {
  padding: 24px;
  max-width: 1200px;
  margin: 0 auto;
}

.sheet-header {
  text-align: center;
  margin-bottom: 32px;

  h2 {
    font-size: 32px;
    margin-bottom: 8px;
    color: #8b4513;
    text-transform: uppercase;
    letter-spacing: 2px;
  }

  .subtitle {
    font-size: 16px;
    color: #666;
  }
}

.character-sheet-container {
  margin-bottom: 24px;
}

.character-sheet {
  border: 3px solid #8b4513;
  background: #f9f6f0;
  font-family: 'Times New Roman', serif;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);

  section {
    border: 2px solid #8b4513;
    padding: 20px;
    margin: 16px;
    background: linear-gradient(to bottom, #fffef8, #f5f2e8);
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);

    h3 {
      border-bottom: 2px solid #8b4513;
      text-transform: uppercase;
      letter-spacing: 2px;
      margin: 0 0 16px 0;
      padding-bottom: 8px;
      font-size: 20px;
      color: #8b4513;
      font-weight: bold;
    }

    h4 {
      font-size: 16px;
      margin: 16px 0 8px 0;
      text-decoration: underline;
      color: #8b4513;
    }
  }
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 12px;

  .info-item {
    display: flex;
    justify-content: space-between;
    padding: 8px 12px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 4px;

    &.full-width {
      grid-column: 1 / -1;
    }

    .label {
      font-weight: bold;
      color: #666;
    }

    .value {
      color: #000;

      &.bonus {
        color: #28a745;
        font-weight: bold;
      }

      &.penalty {
        color: #dc3545;
        font-weight: bold;
      }
    }
  }
}

.abilities-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 12px;

  .ability-box {
    border: 2px solid #8b4513;
    padding: 16px;
    text-align: center;
    background: #fff;
    border-radius: 8px;

    .ability-name {
      font-size: 14px;
      font-weight: bold;
      color: #666;
      margin-bottom: 8px;
    }

    .ability-value {
      font-size: 28px;
      font-weight: bold;
      color: #8b4513;
      margin-bottom: 4px;
    }

    .ability-adjust {
      font-size: 12px;
      color: #666;
    }
  }
}

.exceptional-strength {
  margin-top: 16px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #8b4513;
}

.combat-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
  margin-bottom: 16px;

  .combat-item {
    display: flex;
    justify-content: space-between;
    padding: 8px 12px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 4px;

    .label {
      font-weight: bold;
      color: #666;
    }

    .value {
      color: #000;
      font-weight: bold;
    }
  }
}

.saving-throws {
  background: white;
  padding: 12px;
  border-radius: 4px;
  border: 1px solid #ddd;

  .throws-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 8px;
    font-size: 14px;
  }
}

.proficiency-subsection {
  margin-bottom: 16px;

  ul {
    list-style-type: disc;
    padding-left: 24px;
    margin: 8px 0;

    li {
      margin: 4px 0;
    }
  }
}

.money-info {
  display: flex;
  gap: 24px;
  margin-bottom: 16px;
  font-weight: bold;
  font-size: 16px;
}

.equipment-list {
  .equipment-group {
    margin-bottom: 16px;

    ul {
      list-style-type: disc;
      padding-left: 24px;
      margin: 8px 0;

      li {
        margin: 4px 0;

        .weight {
          color: #666;
          font-size: 14px;
        }
      }
    }
  }
}

.spell-slots {
  margin-bottom: 16px;

  .slots-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 8px;
    margin-top: 8px;

    .bonus {
      color: #28a745;
      font-weight: bold;
    }
  }
}

.spellbook,
.spell-spheres {
  margin-top: 16px;

  div {
    margin: 8px 0;
  }
}

.info-subsection {
  margin-bottom: 16px;

  p {
    white-space: pre-wrap;
    line-height: 1.6;
  }
}

.abilities-list {
  .ability-item {
    margin: 12px 0;
    line-height: 1.6;

    strong {
      color: #8b4513;
    }
  }
}

.subsection {
  margin-top: 16px;

  ul {
    list-style-type: disc;
    padding-left: 24px;
    margin: 8px 0;

    li {
      margin: 4px 0;
      line-height: 1.6;
    }
  }
}

.empty-text {
  color: #999;
  font-style: italic;
}

.bottom-actions {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  padding: 0 16px;

  .adnd-button {
    flex: 1;
    max-width: 300px;
    padding: 16px 32px;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid #8b4513;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;

    .button-icon {
      font-size: 20px;
    }

    &.primary {
      background: linear-gradient(135deg, #d4af37, #c9a227);
      color: white;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);

      &:hover {
        background: linear-gradient(135deg, #e0bb44, #d4af37);
        transform: translateY(-2px);
        box-shadow: 0 6px 8px rgba(0, 0, 0, 0.4);
      }
    }

    &.secondary {
      background: white;
      color: #8b4513;

      &:hover {
        background: #f5f2e8;
        transform: translateY(-2px);
      }
    }
  }
}
