
.game-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: #f5f5dc;
  overflow: hidden;
}

.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #f5f5dc;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.loading-content {
  text-align: center;
  padding: 40px;
  border: 4px solid #000;
  background-color: #fff;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 1px solid #000;
    pointer-events: none;
  }

  p {
    font-family: 'Times New Roman', serif;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
    margin: 20px 0 0 0;
  }
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #000;
  border-top-color: transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

// 
.game-panel {
  display: flex;
  width: 100%;
  height: 100%;
  min-height: 1200px;
  background-image: url('https://www.transparenttextures.com/patterns/old-map.png');
  background-color: #f5f5dc;
  border: 2px solid #000;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  position: relative;
}

// 
.center-pane {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

// 
.mobile-header {
  display: none;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  background-color: #fff;
  border-bottom: 2px solid #000;
  position: relative;
  z-index: 100;

  .mobile-toggle-btn {
    width: 40px;
    height: 40px;
    border: 2px solid #000;
    background-color: #f5f5dc;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    transition: all 0.2s;

    &:hover {
      background-color: #e8dfc4;
    }

    &:active {
      transform: scale(0.95);
    }
  }

  .mobile-title {
    font-family: 'Times New Roman', serif;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
  }
}

// 
.mobile-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1100;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;

  &.visible {
    opacity: 1;
    pointer-events: auto;
  }
}

// 
@media (max-width: 992px) {
  .game-panel {
    flex-direction: column;
    border: none;
  }

  .mobile-header {
    display: flex;
  }

  .mobile-overlay {
    display: block;
  }

  .center-pane {
    width: 100%;
    height: 100%;
  }
}
