
.step5-weapon-proficiency {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

.info-panel {
  background-color: #fff;
  border: 4px solid #000;
  padding: 20px;
  margin-bottom: 20px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    border: 1px solid #666;
    pointer-events: none;
  }

  h2 {
    font-family: 'Times New Roman', serif;
    font-size: 28px;
    font-weight: bold;
    text-align: center;
    margin: 0 0 15px 0;
    text-transform: uppercase;
  }

  .info-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 15px;
  }

  .info-item {
    .label {
      font-weight: bold;
      margin-right: 8px;
    }

    .value {
      &.warning {
        color: #c00;
        font-weight: bold;
      }

      &.success {
        color: #0a0;
        font-weight: bold;
      }
    }
  }
}

.language-conversion {
  background-color: #f9f9f9;
  border: 2px solid #666;
  padding: 15px;
  margin-bottom: 20px;

  h3 {
    font-size: 20px;
    margin: 0 0 10px 0;
  }

  .help-text {
    color: #666;
    margin: 0 0 10px 0;
  }

  .converter {
    display: flex;
    align-items: center;
    gap: 15px;

    .convert-value {
      font-size: 18px;
      font-weight: bold;
      min-width: 60px;
      text-align: center;
    }
  }
}

.specialization-info {
  background-color: #fffbea;
  border: 3px solid #d4af37;
  padding: 15px;
  margin-bottom: 20px;

  h3 {
    font-size: 20px;
    margin: 0 0 10px 0;
    color: #d4af37;
  }

  .spec-details {
    p {
      margin: 10px 0 5px 0;
    }

    ul {
      margin: 5px 0;
      padding-left: 25px;
    }
  }
}

.weapon-categories {
  margin-bottom: 20px;

  .tabs {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  .tab-button {
    padding: 10px 20px;
    border: 2px solid #000;
    background-color: #fff;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background-color: #f0f0f0;
    }

    &.active {
      background-color: #000;
      color: #fff;
    }
  }
}

.weapons-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.weapon-card {
  border: 3px solid #666;
  background-color: #fff;
  padding: 15px;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    border-color: #000;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  }

  &.selected {
    border-color: #4a90e2;
    background-color: #f0f8ff;
  }

  &.specialized {
    border-color: #d4af37;
    background-color: #fffbea;
  }

  .weapon-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }

  .weapon-name {
    display: flex;
    align-items: center;
    gap: 8px;

    .icon {
      font-size: 24px;
    }

    .name {
      font-size: 18px;
      font-weight: bold;
    }

    .english {
      font-size: 14px;
      color: #666;
    }
  }

  .weapon-status {
    .specialized-badge {
      background-color: #d4af37;
      color: #fff;
      padding: 4px 12px;
      border-radius: 12px;
      font-weight: bold;
      font-size: 12px;
    }

    .selected-badge {
      background-color: #4a90e2;
      color: #fff;
      padding: 4px 12px;
      border-radius: 12px;
      font-weight: bold;
      font-size: 12px;
    }
  }

  .weapon-details {
    font-size: 14px;
    line-height: 1.6;

    .stat-row {
      display: flex;
      gap: 15px;
      margin-bottom: 5px;

      span {
        &:not(:last-child)::after {
          content: '|';
          margin-left: 15px;
          color: #ccc;
        }
      }

      .special-note {
        color: #d4af37;
        font-style: italic;
      }
    }
  }

  .weapon-actions {
    margin-top: 10px;
    display: flex;
    gap: 10px;
  }

  .related-weapons {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #ddd;
    font-size: 13px;

    p {
      margin: 5px 0;
    }

    .help-text {
      color: #666;
      font-style: italic;
    }
  }
}

.navigation {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-top: 30px;
}

.adnd-button {
  padding: 12px 24px;
  border: 3px solid #000;
  background-color: #fff;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s;
  text-transform: uppercase;

  &:hover:not(:disabled) {
    background-color: #f0f0f0;
    transform: translateY(-2px);
  }

  &.primary {
    background-color: #000;
    color: #fff;

    &:hover:not(:disabled) {
      background-color: #333;
    }
  }

  &.secondary {
    background-color: #666;
    color: #fff;
    border-color: #666;

    &:hover:not(:disabled) {
      background-color: #888;
    }
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}
