
.spellbook-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: 20px;
}

.spellbook-modal {
  background-color: #f5f5dc;
  border: 4px solid #000;
  width: 90%;
  max-width: 1000px;
  max-height: 85vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 2px solid #666;
    pointer-events: none;
  }
}

.modal-header {
  background-color: #fff;
  border-bottom: 3px solid #000;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .header-content {
    h2 {
      font-family: 'Times New Roman', serif;
      font-size: 24px;
      font-weight: bold;
      margin: 0 0 5px 0;
    }

    .class-info {
      font-size: 14px;
      color: #666;
      margin: 0;
    }
  }

  .close-btn {
    background: none;
    border: 2px solid #000;
    width: 40px;
    height: 40px;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;

    &:hover {
      background-color: #000;
      color: #fff;
    }
  }
}

.tabs-bar {
  background-color: #e8e8d0;
  border-bottom: 2px solid #000;
  display: flex;
  gap: 5px;
  padding: 10px;

  .tab-btn {
    flex: 1;
    padding: 10px 20px;
    border: 2px solid #000;
    background-color: #fff;
    font-family: 'Times New Roman', serif;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background-color: #f0f0f0;
    }

    &.active {
      background-color: #9370db;
      color: #fff;
      border-color: #000;
    }
  }
}

.modal-body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background-color: #f5f5f5;
}

.tab-content {
  animation: fadeIn 0.3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

// 
.info-bar {
  display: flex;
  gap: 20px;
  padding: 15px;
  background-color: #fff;
  border: 2px solid #000;
  margin-bottom: 20px;

  .info-item {
    display: flex;
    align-items: center;
    gap: 8px;

    .label {
      font-weight: bold;
      color: #666;
    }

    .value {
      color: #9370db;
      font-weight: bold;
      font-size: 16px;
    }
  }
}

// 
.info-banner {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 15px;
  background-color: #fff3cd;
  border: 2px solid #ffc107;
  margin-bottom: 15px;
  border-radius: 4px;

  .info-icon {
    font-size: 20px;
  }

  .info-text {
    flex: 1;
    color: #856404;
    font-weight: 500;
  }
}

// 
.action-bar {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;

  .primary-btn,
  .danger-btn {
    padding: 10px 20px;
    border: 2px solid #000;
    font-family: 'Times New Roman', serif;
    font-weight: bold;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 8px;
    transition: all 0.2s;

    span {
      font-size: 16px;
    }
  }

  .primary-btn {
    background-color: #9370db;
    color: #fff;

    &:hover {
      background-color: #7b68ee;
    }
  }

  .danger-btn {
    background-color: #fff;
    color: #dc3545;
    border-color: #dc3545;

    &:hover {
      background-color: #dc3545;
      color: #fff;
    }
  }
}

// 
.learned-spells {
  .spell-level-group {
    margin-bottom: 20px;

    .level-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 15px;
      background-color: #9370db;
      color: #fff;
      border: 2px solid #000;
      font-family: 'Times New Roman', serif;
      font-weight: bold;

      .level {
        font-size: 16px;
      }

      .count {
        font-size: 14px;
      }
    }

    .spell-list {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
      gap: 10px;
      padding: 10px;
      background-color: #fff;
      border: 2px solid #000;
      border-top: none;

      .spell-item {
        padding: 10px;
        border: 2px solid #9370db;
        background-color: #f9f9f9;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
          background-color: #e6d8f5;
          transform: translateY(-2px);
          box-shadow: 0 2px 8px rgba(147, 112, 219, 0.3);
        }

        .spell-name {
          font-weight: bold;
          margin-bottom: 5px;
        }

        .spell-school {
          font-size: 12px;
          color: #666;
        }
      }
    }
  }
}

// 
.spell-slots {
  .slot-level-group {
    margin-bottom: 20px;

    .slot-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 15px;
      background-color: #4682b4;
      color: #fff;
      border: 2px solid #000;
      font-family: 'Times New Roman', serif;
      font-weight: bold;

      .level {
        font-size: 16px;
      }

      .slot-count {
        font-size: 14px;
      }
    }

    .memorized-spells {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
      gap: 10px;
      padding: 10px;
      background-color: #fff;
      border: 2px solid #000;
      border-top: none;

      .memorized-spell {
        padding: 10px;
        border: 2px solid #4682b4;
        background-color: #e3f2fd;
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
          background-color: #bbdefb;
          transform: translateY(-2px);
          box-shadow: 0 2px 8px rgba(70, 130, 180, 0.3);
        }

        .spell-name {
          font-weight: bold;
          flex: 1;
        }

        .view-icon {
          font-size: 16px;
          opacity: 0.6;
          transition: opacity 0.2s;
        }

        &:hover .view-icon {
          opacity: 1;
        }
      }

      .empty-slot {
        padding: 10px;
        border: 2px dashed #999;
        background-color: #f9f9f9;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        min-height: 60px;
        transition: all 0.2s;

        &:hover {
          border-color: #4682b4;
          background-color: #e3f2fd;
        }

        .plus-icon {
          font-size: 24px;
          color: #999;
        }

        .hint {
          font-size: 12px;
          color: #666;
          margin-top: 5px;
        }
      }
    }
  }
}

// 
.empty-state {
  text-align: center;
  padding: 60px 20px;
  background-color: #fff;
  border: 3px dashed #ccc;

  .empty-icon {
    font-size: 48px;
    margin-bottom: 15px;
  }

  p {
    font-family: 'Times New Roman', serif;
    font-size: 16px;
    margin: 10px 0;
    color: #666;

    &.hint {
      font-size: 13px;
      color: #999;
      font-style: italic;
    }
  }
}

// 
.dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2100;
}

.dialog {
  background-color: #f5f5dc;
  border: 4px solid #000;
  width: 90%;
  max-width: 800px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;

  .dialog-header {
    background-color: #fff;
    border-bottom: 3px solid #000;
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    h3 {
      font-family: 'Times New Roman', serif;
      font-size: 20px;
      margin: 0;
    }
  }

  .dialog-body {
    flex: 1;
    overflow-y: auto;
    padding: 20px;

    .level-selector {
      display: flex;
      gap: 10px;
      margin-bottom: 20px;
      flex-wrap: wrap;

      .level-btn {
        padding: 8px 16px;
        border: 2px solid #000;
        background-color: #fff;
        font-weight: bold;
        cursor: pointer;
        transition: all 0.2s;

        &:hover:not(.disabled) {
          background-color: #f0f0f0;
        }

        &.active {
          background-color: #9370db;
          color: #fff;
        }

        &.disabled {
          opacity: 0.5;
          cursor: not-allowed;
        }
      }
    }

    .spell-selection-list {
      display: flex;
      flex-direction: column;
      gap: 10px;

      .selectable-spell {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px;
        border: 2px solid #000;
        background-color: #fff;
        transition: all 0.2s;

        &:hover {
          background-color: #f9f9f9;
        }

        .spell-info {
          flex: 1;
          cursor: pointer;
          padding-right: 15px;

          &:hover {
            .spell-name {
              color: #9370db;
            }
          }

          .spell-name {
            font-weight: bold;
            font-size: 16px;
            margin-bottom: 5px;
            transition: color 0.2s;

            .view-hint {
              font-size: 12px;
              color: #999;
              font-weight: normal;
              margin-left: 8px;
            }
          }

          .spell-meta {
            font-size: 12px;
            color: #666;
          }
        }

        .learn-btn,
        .select-btn {
          padding: 8px 20px;
          border: 2px solid #000;
          background-color: #9370db;
          color: #fff;
          font-weight: bold;
          cursor: pointer;
          transition: all 0.2s;
          flex-shrink: 0;

          &:hover {
            background-color: #7b68ee;
          }
        }
      }

      .empty-hint {
        text-align: center;
        padding: 40px 20px;
        color: #999;
        font-style: italic;
      }
    }
  }
}
