
.step8-spell-selection {
  width: 100%;
  min-height: 600px;
  padding: 30px;
  font-family: 'Times New Roman', serif;
}

.selection-header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 3px solid #000;

  h3 {
    font-size: 28px;
    font-weight: bold;
    margin: 0 0 12px 0;
    text-transform: uppercase;
    letter-spacing: 2px;
  }

  .character-info {
    display: flex;
    justify-content: center;
    gap: 20px;
    font-size: 16px;
    color: #666;

    span {
      padding: 6px 12px;
      background-color: #f5f5f5;
      border: 2px solid #ddd;
      border-radius: 4px;
    }
  }
}

.phase-container {
  background-color: #fafafa;
  border: 3px solid #000;
  padding: 30px;
  margin-bottom: 20px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 1px solid #666;
    pointer-events: none;
  }
}

.phase-header {
  margin-bottom: 24px;

  h4 {
    font-size: 24px;
    font-weight: bold;
    margin: 0 0 12px 0;
  }

  .hint {
    color: #666;
    font-size: 16px;
    line-height: 1.6;
  }

  .progress-info {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 12px;

    .count {
      font-size: 18px;
      font-weight: bold;
      color: #000;
    }

    .hint {
      font-size: 14px;
      color: #999;
    }
  }
}

.spellbook-roll-phase,
.non-caster-notice {
  .phase-content {
    text-align: center;

    h4 {
      font-size: 24px;
      margin-bottom: 20px;
    }

    .instruction {
      font-size: 16px;
      line-height: 1.8;
      color: #666;
      margin-bottom: 30px;
    }

    .dice-roll-area {
      .large {
        padding: 16px 40px;
        font-size: 18px;
      }
    }
  }
}

.spell-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 20px;
  margin-bottom: 20px;

  &.compact {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 16px;
  }

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.phase-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  padding-top: 20px;
  border-top: 2px solid #ddd;
}

.memorization-content {
  display: grid;
  grid-template-columns: 350px 1fr;
  gap: 30px;

  @media (max-width: 1024px) {
    grid-template-columns: 1fr;
  }
}

.spell-slots-section,
.spellbook-section,
.available-spells-section {
  h5 {
    font-size: 20px;
    font-weight: bold;
    margin: 0 0 16px 0;
    padding-bottom: 12px;
    border-bottom: 2px solid #000;
  }
}

.spell-slots-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.slot-level {
  background-color: #fff;
  border: 2px solid #ddd;
  padding: 12px;
  border-radius: 4px;

  &.has-slots {
    border-color: #000;
  }

  .slot-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;

    .level-label {
      font-weight: bold;
      font-size: 16px;
    }

    .slot-count {
      font-size: 14px;
      color: #666;

      .bonus-indicator {
        color: #5cb85c;
        font-weight: bold;
      }
    }
  }

  .memorized-spells {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .memorized-spell-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 12px;
    background-color: #e3f2fd;
    border: 1px solid #1976d2;
    border-radius: 4px;

    .spell-name {
      font-size: 14px;
      font-weight: 500;
    }

    .remove-btn {
      width: 24px;
      height: 24px;
      border: none;
      background-color: #d9534f;
      color: #fff;
      border-radius: 50%;
      cursor: pointer;
      font-size: 18px;
      line-height: 1;
      transition: background-color 0.2s;

      &:hover {
        background-color: #c9302c;
      }
    }
  }

  .empty-slot {
    padding: 8px 12px;
    background-color: #f5f5f5;
    border: 1px dashed #ccc;
    border-radius: 4px;
    text-align: center;
    color: #999;
    font-size: 14px;
  }
}

.sphere-group {
  margin-bottom: 30px;

  .sphere-title {
    font-size: 18px;
    font-weight: bold;
    margin: 0 0 16px 0;
    padding: 8px 12px;
    background-color: #fff3e0;
    border-left: 4px solid #e65100;
  }
}

.warning-box {
  margin-top: 16px;
  padding: 12px 16px;
  background-color: #fff3cd;
  border: 2px solid #ffc107;
  border-radius: 4px;
  color: #856404;
  font-weight: 500;
}

.bottom-actions {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  border-top: 3px solid #000;

  .adnd-button {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 32px;
    font-size: 16px;
    font-family: 'Times New Roman', serif;
    font-weight: bold;
    border: 3px solid #000;
    cursor: pointer;
    transition: all 0.2s ease;
    text-transform: uppercase;
    letter-spacing: 1px;

    &.primary {
      background-color: #000;
      color: #fff;

      &:hover:not(:disabled) {
        background-color: #333;
      }

      &:disabled {
        background-color: #ccc;
        border-color: #999;
        cursor: not-allowed;
        opacity: 0.6;
      }
    }

    &.secondary {
      background-color: #fff;
      color: #000;

      &:hover {
        background-color: #f5f5f5;
      }
    }

    .button-icon {
      font-size: 20px;
    }
  }
}

@media (max-width: 768px) {
  .step8-spell-selection {
    padding: 20px;
  }

  .selection-header h3 {
    font-size: 22px;
  }

  .phase-container {
    padding: 20px;
  }

  .memorization-content {
    grid-template-columns: 1fr;
  }
}
