
.step10-basic-info {
  width: 100%;
  min-height: 600px;
  padding: 30px;
  font-family: 'Times New Roman', serif;
}

.info-header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 3px solid #000;

  h3 {
    font-size: 28px;
    font-weight: bold;
    margin: 0 0 12px 0;
    text-transform: uppercase;
    letter-spacing: 2px;
  }

  .character-summary {
    display: flex;
    justify-content: center;
    gap: 15px;
    font-size: 16px;
    color: #666;

    span {
      padding: 6px 12px;
      background-color: #f5f5f5;
      border: 2px solid #ddd;
      border-radius: 4px;
    }
  }
}

.info-content {
  max-width: 800px;
  margin: 0 auto;
}

.form-section {
  margin-bottom: 24px;

  &:last-of-type {
    margin-bottom: 30px;
  }
}

.form-label {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: bold;

  .label-text {
    color: #000;
  }

  .label-hint {
    font-size: 14px;
    font-weight: normal;
    color: #666;
  }

  .label-required {
    color: #d9534f;
    font-size: 18px;
  }
}

.form-input,
.form-select {
  width: 100%;
  padding: 12px 16px;
  font-size: 16px;
  font-family: 'Times New Roman', serif;
  border: 2px solid #000;
  background-color: #fff;
  transition: all 0.2s ease;

  &:focus {
    outline: none;
    border-color: #1976d2;
    box-shadow: 0 0 0 3px rgba(25, 118, 210, 0.1);
  }

  &::placeholder {
    color: #999;
  }
}

.form-select {
  cursor: pointer;

  optgroup {
    font-weight: bold;
    font-style: italic;
  }

  option {
    padding: 8px;
  }
}

.form-textarea {
  width: 100%;
  padding: 12px 16px;
  font-size: 15px;
  font-family: 'Times New Roman', serif;
  line-height: 1.6;
  border: 2px solid #000;
  background-color: #fff;
  resize: vertical;
  transition: all 0.2s ease;

  &:focus {
    outline: none;
    border-color: #1976d2;
    box-shadow: 0 0 0 3px rgba(25, 118, 210, 0.1);
  }

  &::placeholder {
    color: #999;
  }
}

.char-count {
  text-align: right;
  font-size: 12px;
  color: #666;
  margin-top: 4px;
}

.gender-options {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.gender-option {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border: 3px solid #000;
  background-color: #fff;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }

  &.selected {
    background-color: #e3f2fd;
    border-color: #1976d2;
    box-shadow: 0 4px 12px rgba(25, 118, 210, 0.3);
  }

  .gender-radio {
    position: absolute;
    opacity: 0;
    pointer-events: none;
  }

  .gender-icon {
    font-size: 32px;
    margin-bottom: 8px;
  }

  .gender-label {
    font-size: 16px;
    font-weight: bold;
    color: #000;
  }
}

.info-notice {
  display: flex;
  gap: 16px;
  padding: 20px;
  margin-bottom: 20px;
  background-color: #f8f9fa;
  border: 2px solid #000;
  border-radius: 8px;

  .notice-icon {
    font-size: 32px;
    flex-shrink: 0;
  }

  .notice-content {
    flex: 1;

    p {
      font-size: 15px;
      line-height: 1.6;
      margin: 0;
      color: #333;
    }
  }
}

.bottom-actions {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  border-top: 3px solid #000;

  .adnd-button {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 32px;
    font-size: 16px;
    font-family: 'Times New Roman', serif;
    font-weight: bold;
    border: 3px solid #000;
    cursor: pointer;
    transition: all 0.2s ease;
    text-transform: uppercase;
    letter-spacing: 1px;

    &.primary {
      background-color: #000;
      color: #fff;

      &:hover:not(:disabled) {
        background-color: #333;
      }

      &:disabled {
        background-color: #ccc;
        border-color: #999;
        cursor: not-allowed;
        opacity: 0.6;
      }
    }

    &.secondary {
      background-color: #fff;
      color: #000;

      &:hover {
        background-color: #f5f5f5;
      }
    }

    .button-icon {
      font-size: 20px;
    }
  }
}

@media (max-width: 768px) {
  .step10-basic-info {
    padding: 20px;
  }

  .info-header h3 {
    font-size: 22px;
  }

  .character-summary {
    flex-direction: column;
    gap: 8px;
  }
}
