
.spell-detail-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3000;
  padding: 20px;
}

.spell-detail-modal {
  background-color: #f5f5dc;
  border: 4px solid #000;
  width: 90%;
  max-width: 800px;
  max-height: 85vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 2px solid #666;
    pointer-events: none;
  }
}

.modal-header {
  background-color: #fff;
  border-bottom: 3px solid #000;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;

  h2 {
    font-family: 'Times New Roman', serif;
    font-size: 22px;
    font-weight: bold;
    margin: 0;
  }

  .close-btn {
    background: none;
    border: 2px solid #000;
    width: 40px;
    height: 40px;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;

    &:hover {
      background-color: #000;
      color: #fff;
    }
  }
}

.modal-body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background-color: #fff;

  &.empty {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #999;
    font-style: italic;
  }
}

.spell-header {
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 3px solid #000;

  .spell-title-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;

    .spell-name {
      font-family: 'Times New Roman', serif;
      font-size: 24px;
      font-weight: bold;
      margin: 0;
      color: #9370db;
    }

    .spell-level-badge {
      padding: 5px 15px;
      background-color: #9370db;
      color: #fff;
      border: 2px solid #000;
      font-weight: bold;
      font-size: 14px;
    }
  }

  .spell-english {
    font-size: 14px;
    color: #666;
    font-style: italic;
    margin: 0;
  }
}

.spell-properties {
  background-color: #f9f9f9;
  border: 2px solid #000;
  padding: 15px;
  margin-bottom: 20px;

  .property-row {
    display: flex;
    padding: 8px 0;
    border-bottom: 1px solid #ddd;

    &:last-child {
      border-bottom: none;
    }

    .property-label {
      font-weight: bold;
      min-width: 100px;
      color: #333;
    }

    .property-value {
      flex: 1;
      color: #666;

      &.material {
        color: #8b4513;
        font-style: italic;
      }

      &.reversible {
        color: #9370db;
        font-weight: bold;
      }
    }
  }
}

.spell-description {
  h4 {
    font-family: 'Times New Roman', serif;
    font-size: 18px;
    font-weight: bold;
    margin: 0 0 15px 0;
    padding-bottom: 8px;
    border-bottom: 2px solid #000;
  }

  p {
    font-family: Georgia, serif;
    font-size: 15px;
    line-height: 1.8;
    color: #333;
    text-align: justify;
    margin: 0;
    white-space: pre-wrap;
  }
}
