
.step6-nonweapon-proficiency {
  max-width: 1600px;
  margin: 0 auto;
  padding: 20px;
}

.main-container {
  display: grid;
  grid-template-columns: 500px 1fr;
  gap: 20px;
  margin-bottom: 20px;
}

.left-panel {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.info-panel {
  background-color: #fff;
  border: 4px solid #000;
  padding: 15px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    border: 1px solid #666;
    pointer-events: none;
  }

  h2 {
    font-family: 'Times New Roman', serif;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin: 0 0 10px 0;
  }

  .info-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    font-size: 14px;
  }

  .info-item {
    .label {
      font-weight: bold;
      margin-right: 5px;
    }

    .value {
      &.warning {
        color: #c00;
        font-weight: bold;
      }

      &.success {
        color: #0a0;
        font-weight: bold;
      }
    }
  }
}

.language-conversion {
  background-color: #f9f9f9;
  border: 2px solid #666;
  padding: 12px;

  h3 {
    font-size: 16px;
    margin: 0 0 8px 0;
  }

  .help-text {
    color: #666;
    font-size: 13px;
    margin: 0 0 8px 0;
  }

  .converter {
    display: flex;
    align-items: center;
    gap: 10px;

    .convert-value {
      font-size: 16px;
      font-weight: bold;
      min-width: 40px;
      text-align: center;
    }
  }
}

.group-tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;

  .tab-button {
    padding: 8px 16px;
    border: 2px solid #000;
    background-color: #fff;
    font-weight: bold;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background-color: #f0f0f0;
    }

    &.active {
      background-color: #000;
      color: #fff;
    }
  }
}

.proficiency-list {
  background-color: #fff;
  border: 3px solid #000;
  max-height: 600px;
  overflow-y: auto;
}

.prof-item {
  padding: 12px;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #f8f8f8;
  }

  &.selected {
    background-color: #e8f4ff;
  }

  &.highlight {
    background-color: #fff4cc;
    border-left: 4px solid #d4af37;
  }

  .prof-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;

    .prof-name {
      font-weight: bold;
      font-size: 15px;
    }

    .prof-cost {
      font-size: 13px;
      color: #666;

      .selected-count {
        color: #4a90e2;
        font-weight: bold;
      }
    }
  }

  .prof-meta {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    color: #888;
  }
}

.right-panel {
  background-color: #fff;
  border: 4px solid #000;
  padding: 20px;
  max-height: 800px;
  overflow-y: auto;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    border: 1px solid #666;
    pointer-events: none;
  }
}

.prof-details {
  .details-header {
    margin-bottom: 15px;

    h2 {
      font-family: 'Times New Roman', serif;
      font-size: 28px;
      font-weight: bold;
      margin: 0 0 5px 0;
    }

    .english-name {
      font-size: 16px;
      color: #666;
      font-style: italic;
    }
  }

  .details-stats {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin-bottom: 20px;
    padding: 15px;
    background-color: #f9f9f9;
    border: 2px solid #ddd;

    .stat-item {
      .label {
        font-weight: bold;
        margin-right: 5px;
      }
    }
  }

  .details-description {
    margin-bottom: 20px;

    h3 {
      font-size: 20px;
      margin: 0 0 10px 0;
      border-bottom: 2px solid #000;
      padding-bottom: 5px;
    }

    .description-text {
      line-height: 1.8;
      font-size: 15px;

      ::v-deep(p) {
        margin-bottom: 10px;
      }
    }
  }

  .details-actions {
    .selected-info {
      p {
        margin: 5px 0;
        font-weight: bold;

        &.help-text {
          font-weight: normal;
          color: #666;
          font-size: 13px;
        }
      }

      .action-buttons {
        display: flex;
        gap: 10px;
        margin-top: 10px;
      }
    }
  }
}

.no-selection {
  text-align: center;
  padding: 100px 20px;
  color: #999;
  font-size: 18px;
}

.navigation {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
}

.adnd-button {
  padding: 12px 24px;
  border: 3px solid #000;
  background-color: #fff;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s;
  text-transform: uppercase;

  &:hover:not(:disabled) {
    background-color: #f0f0f0;
    transform: translateY(-2px);
  }

  &.primary {
    background-color: #000;
    color: #fff;

    &:hover:not(:disabled) {
      background-color: #333;
    }
  }

  &.secondary {
    background-color: #666;
    color: #fff;
    border-color: #666;

    &:hover:not(:disabled) {
      background-color: #888;
    }
  }

  &.danger {
    background-color: #c00;
    color: #fff;
    border-color: #c00;

    &:hover:not(:disabled) {
      background-color: #e00;
    }
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}
