
.step9-alignment-selection {
  width: 100%;
  min-height: 600px;
  padding: 30px;
  font-family: 'Times New Roman', serif;
}

.selection-header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 3px solid #000;

  h3 {
    font-size: 28px;
    font-weight: bold;
    margin: 0 0 12px 0;
    text-transform: uppercase;
    letter-spacing: 2px;
  }

  .character-info {
    display: flex;
    justify-content: center;
    gap: 20px;
    font-size: 16px;
    color: #666;

    span {
      padding: 6px 12px;
      background-color: #f5f5f5;
      border: 2px solid #ddd;
      border-radius: 4px;
    }
  }
}

.alignment-notice,
.restriction-notice {
  display: flex;
  gap: 16px;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
  border: 2px solid #000;
  background-color: #f8f9fa;

  .notice-icon,
  .restriction-icon {
    font-size: 32px;
    flex-shrink: 0;
  }

  .notice-content,
  .restriction-content {
    flex: 1;

    h4 {
      font-size: 18px;
      font-weight: bold;
      margin: 0 0 8px 0;
    }

    p {
      font-size: 15px;
      line-height: 1.6;
      margin: 0;
      color: #333;

      strong {
        color: #d9534f;
        font-weight: bold;
      }
    }
  }
}

.restriction-notice {
  background-color: #fff3cd;
  border-color: #ffc107;

  .restriction-content p {
    color: #856404;
    font-weight: 500;
  }
}

.selection-content {
  display: grid;
  grid-template-columns: 1fr 400px;
  gap: 30px;
  margin-bottom: 30px;

  @media (max-width: 1024px) {
    grid-template-columns: 1fr;
  }
}

.alignment-grid-container {
  h4 {
    font-size: 20px;
    font-weight: bold;
    margin: 0 0 20px 0;
    padding-bottom: 12px;
    border-bottom: 2px solid #000;
  }
}

.alignment-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.alignment-card {
  position: relative;
  padding: 20px;
  background-color: #fff;
  border: 3px solid #000;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: center;

  &:hover:not(.disabled) {
    transform: translateY(-4px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  }

  &.selected {
    background-color: #e3f2fd;
    border-color: #1976d2;
    box-shadow: 0 4px 12px rgba(25, 118, 210, 0.3);
  }

  &.disabled {
    opacity: 0.4;
    cursor: not-allowed;
    background-color: #f5f5f5;

    &:hover {
      transform: none;
      box-shadow: none;
    }
  }

  .alignment-icon {
    font-size: 32px;
    margin-bottom: 12px;
  }

  .alignment-name {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 4px;
    color: #000;
  }

  .alignment-english {
    font-size: 12px;
    color: #666;
    font-style: italic;
  }

  .selected-indicator,
  .disabled-indicator {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: bold;
  }

  .selected-indicator {
    background-color: #1976d2;
    color: #fff;
  }

  .disabled-indicator {
    background-color: #999;
    color: #fff;
  }
}

.alignment-details {
  background-color: #fafafa;
  border: 3px solid #000;
  padding: 24px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 1px solid #666;
    pointer-events: none;
  }

  .details-content {
    position: relative;
    z-index: 1;
  }

  .no-selection {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 300px;
    color: #999;
    font-size: 18px;
  }
}

.detail-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 20px;

  .detail-icon {
    font-size: 48px;
  }

  h2 {
    font-size: 24px;
    font-weight: bold;
    margin: 0;
  }

  .english-name {
    font-size: 14px;
    color: #666;
    font-style: italic;
    margin: 4px 0 0 0;
  }
}

.divider {
  height: 2px;
  background-color: #000;
  margin: 20px 0;
}

.detail-section {
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  h4 {
    font-size: 16px;
    font-weight: bold;
    margin: 0 0 12px 0;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  .description,
  .examples {
    font-size: 15px;
    line-height: 1.7;
    color: #333;
    margin: 0;
  }

  .examples {
    font-style: italic;
    color: #666;
  }
}

.bottom-actions {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  border-top: 3px solid #000;

  .adnd-button {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 32px;
    font-size: 16px;
    font-family: 'Times New Roman', serif;
    font-weight: bold;
    border: 3px solid #000;
    cursor: pointer;
    transition: all 0.2s ease;
    text-transform: uppercase;
    letter-spacing: 1px;

    &.primary {
      background-color: #000;
      color: #fff;

      &:hover:not(:disabled) {
        background-color: #333;
      }

      &:disabled {
        background-color: #ccc;
        border-color: #999;
        cursor: not-allowed;
        opacity: 0.6;
      }
    }

    &.secondary {
      background-color: #fff;
      color: #000;

      &:hover {
        background-color: #f5f5f5;
      }
    }

    .button-icon {
      font-size: 20px;
    }
  }
}

@media (max-width: 768px) {
  .step9-alignment-selection {
    padding: 20px;
  }

  .selection-header h3 {
    font-size: 22px;
  }

  .selection-content {
    grid-template-columns: 1fr;
  }

  .alignment-grid {
    grid-template-columns: 1fr;
  }
}
